(function() {
  'use strict';

  angular
    .module('ghecko')
    .service('ProfileService', ProfileService);


  function ProfileService($http,API) {
    var public_api = {
      getInformation : getInformation,
      updateInformation : updateInformation,
      getPost : getPost
    };
    return public_api;

    function getInformation(id) {
      return $http.get(API.url + '/normal-users/'+id);
    }

    function updateInformation(info){
      return $http.put(API.url+'/normal-users',info);
    }

    function getPost(pid){
      return $http.get(API.url+'/normal-users/'+pid+'/posts');
    }


  }
})();

