(function() {
  'use strict';

  angular
    .module('ghecko')
    .controller('LoginController', LoginController);


  function LoginController(LoginService,$state,$rootScope,ProfileService) {
    var vm = this;
    vm.login = function(){
      LoginService.auth(vm.user).success(function (data) {
        localStorage.setItem('userId',data.userId); // console.log(data);
        $rootScope.$broadcast('isLogged',{});
        ProfileService.getInformation(data.userId)
            .success(function (data) {
              localStorage.setItem('profilephoto',data.profilephoto);
              localStorage.setItem('username',data.username);
              $state.go('homefeed');
            })

      });
    }



  }
})();

(function() {
  'use strict';

  angular
    .module('ghecko')
    .controller('HomeFeedController', HomeFeedController);


  function HomeFeedController(HomeService) {
    var vm = this;

    function init() {
      HomeService.getPosts()
          .success(function(data){
            buildPosts(data)
              console.log(data);
          });

    }
    init();

      function buildPosts(posts) {
          for(var i =0; i < posts.length; i++){
              posts[i].customDate = moment(posts[i].date).fromNow();
              for(var j=0; j < posts[i].userlikes.length; j++){
                  if(posts[i].userlikes[j] == localStorage.getItem('userId')){
                      posts[i].isLiked = true;
                  }

              }
          }
          for(var i =0; i < posts.length; i++){
              for(var x=0; x < posts[i].comments.length;x++ ){
                  posts[i].comments[x].customDate = moment(posts[i].comments[x].date).fromNow();

              }
          }

          console.log(posts);
          vm.posts = posts;
      }

      vm.addLike = function (post) {

          if(post.isLiked==true){
              post.likes = post.likes-1;
              for (var i =0; i<post.userlikes.length;i++){
                 if(post.userlikes[i]==localStorage.getItem('userId')){
                     post.userlikes.splice(i,1);
                 }
              }
              post.isLiked = false;
              HomeService.addLike(post)
                  .success(function (data) {
                      init();

                  })

          }else{
              post.likes = post.likes+1;
              post.userlikes.push(localStorage.getItem('userId'));
              HomeService.addLike(post)
                  .success(function (data) {
                      init();

                  })
          }

      }

      vm.send = function ( pcomment,post) {
          var comment = {
              comment: pcomment,
              username: localStorage.getItem('username'),
              userphoto: localStorage.getItem('profilephoto'),
              date: new Date()
          }
          post.comments.push(comment);
          HomeService.addLike(post)
              .success(function (data) {
                  vm.postComment = null;
                  init();

              })


      }




  }
})();

