(function() {
  'use strict';

  angular
    .module('ghecko')
    .config(routerConfig);

  /** @ngInject */
  function routerConfig($stateProvider, $urlRouterProvider) {
    $stateProvider
      .state('homefeed', {
        url: '/',
        templateUrl: 'app/components/home/homeFeed.view.html',
        controller: 'HomeFeedController',
        controllerAs: 'vm'
      })
      .state('login', {
        url: '/login',
        templateUrl: 'app/components/login/login.view.html',
        controller: 'LoginController',
        controllerAs: 'vm'
      })
      .state('register', {
        url: '/register',
        templateUrl: 'app/components/login/register.view.html',
        controller: 'RegisterController',
        controllerAs: 'vm'
      })
      .state('messages', {
        url: '/messages',
        templateUrl: 'app/components/messages/messages.view.html',
        controller: 'MessagesController',
        controllerAs: 'vm'
      })
      .state('notifications', {
        url: '/notifications',
        templateUrl: 'app/components/notifications/notifications.view.html',
        controller: 'NotificationsController',
        controllerAs: 'vm'
      })
      .state('profile', {
        url: '/profile/:id',
        templateUrl: 'app/components/profile/profile.view.html',
        controller: 'ProfileController',
        controllerAs: 'vm'
      })
      .state('search', {
        url: '/search',
        templateUrl: 'app/components/search/search.view.html',
        controller: 'SearchController',
        controllerAs: 'vm'
      })
      .state('place', {
        url: '/place',
        templateUrl: 'app/components/place/place.view.html',
        controller: 'PlaceController',
        controllerAs: 'vm'
      })
      .state('editProfile', {
        url: '/editProfile',
        templateUrl: 'app/components/editProfile/editProfile.view.html',
        controller: 'EditProfileController',
        controllerAs: 'vm'
      })
      .state('addPhoto', {
        url: '/addPhoto',
        templateUrl: 'app/components/addPhoto/addPhoto.view.html',
        controller: 'AddPhotoController',
        controllerAs: 'vm'
      });

    $urlRouterProvider.otherwise('/login');
  }

})();

